import React, { useState } from "react";
import { Flex, Text } from "@chakra-ui/react";
import "../App.css"; // Import the CSS file
import { NavLink } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import './BaseCss/Search.css';
import './BaseCss/Buttons.css'
const Navbar = ({handleSearch}) => {
  const [search,setSearch]=useState('')

  const navigate = useNavigate();
  const LogOut=()=>{
    localStorage.removeItem('token');
    window.location.reload();
    navigate('/login')
  }

  const handelSubmit=async(e)=>{
    e.preventDefault()
    handleSearch(search);
  
  }

  const handlechange=(e)=>{
    e.preventDefault();
    
    setSearch(e.target.value)
  }
    return (
      <div className="navbar-container mb-5" style={{ marginBottom: "15px" }}>
            <Flex as="nav" className="navbar" direction={{ base: "column", md: "row" }} alignItems="center" justify="space-between" wrap="wrap">
                {/* Left side: Name of Blog App */}
                <Text fontSize={{ base: "xl", md: "2xl" }} fontWeight="bold" marginRight="8" color="white">
                    <b>Blog-App</b>
                </Text>

                <div className="searchBox">
                    <form onSubmit={handelSubmit}>
                        <input className="searchInput" type="text" name="search" onChange={handlechange} placeholder="Search something" />
                        <button className="searchButton" type="submit">
                            <svg xmlns="http://www.w3.org/2000/svg" width="29" height="29" viewBox="0 0 29 29" fill="none">
                                <g clipPath="url(#clip0_2_17)">
                                    <g filter="url(#filter0_d_2_17)">
                                        <path d="M23.7953 23.9182L19.0585 19.1814M19.0585 19.1814C19.8188 18.4211 20.4219 17.5185 20.8333 16.5251C21.2448 15.5318 21.4566 14.4671 21.4566 13.3919C21.4566 12.3167 21.2448 11.252 20.8333 10.2587C20.4219 9.2653 19.8188 8.36271 19.0585 7.60242C18.2982 6.84214 17.3956 6.23905 16.4022 5.82759C15.4089 5.41612 14.3442 5.20435 13.269 5.20435C12.1938 5.20435 11.1291 5.41612 10.1358 5.82759C9.1424 6.23905 8.23981 6.84214 7.47953 7.60242C5.94407 9.13789 5.08145 11.2204 5.08145 13.3919C5.08145 15.5634 5.94407 17.6459 7.47953 19.1814C9.01499 20.7168 11.0975 21.5794 13.269 21.5794C15.4405 21.5794 17.523 20.7168 19.0585 19.1814Z" stroke="white" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round" shapeRendering="crispEdges"></path>
                                    </g>
                                </g>
                            </svg>
                        </button>
                    </form>
                </div>

                {/* Right side: Login and Register Buttons */}
                <Flex alignItems="center">
                    <NavLink to="/login">
                        {/* Add your login button content here */}
                    </NavLink>
                    <NavLink to="/create_blog">
                        <h3 className="m-4" style={{ fontSize: "Bold" }}>Create</h3>
                    </NavLink>
                    <NavLink to="/all_blogs">
                        <h3 className="m-4" style={{ fontSize: "Bold" }}>Blogs</h3>
                    </NavLink>
                    <NavLink to="/my_blogs">
                        <h3 className="m-4" style={{ fontSize: "Bold" }}>My-Blogs</h3>
                    </NavLink>
                    <NavLink to="/login">
                        <button className="btn" onClick={LogOut}> Log-out
                        </button>
                    </NavLink>
                </Flex>
            </Flex>
        </div>
    );
};

export default Navbar;
