import React, { useEffect, useState } from 'react';
import HomeNav from '../HomeNav'
import axios from 'axios';
import {useNavigate} from 'react-router-dom'
const CardForm = () => {
    const navigate = useNavigate();
    const [user, setId] = useState(null);
    const [data, setData] = useState({
        title: "",
        description: "",
        image: ""
    });

    const handelChange = (e) => {
        const { name, value } = e.target;
    
        setData({
            ...data,
            [name]: value
        });
    };
    

    useEffect(() => {
        const user = sessionStorage.getItem('user');
        if (user) {
            const parsedUser = JSON.parse(user);
            setId(parsedUser._id);
        }
    }, []);
    console.log(data)
    const handleSubmit =  async(e) => {
        e.preventDefault();

        try {
            const response = await axios.post('https://mernback-85dt.onrender.com/api/post/create-blog', { ...data, user });
            console.log(response.data);
            if (response.status == 201 ) {
                alert("Post Created Successfully");
                navigate('/all_blogs')
            }
        } catch (error) {
            console.error("Error creating post:", error.message);
            // Handle error
        }
    }



    return (
        <>
        <HomeNav/>
        <div className="max-w-md mx-auto mt-20 p-6 bg-white rounded-lg shadow-md mt-5 " style={{marginTop:'34px'}}>
            <h2 className="text-2xl font-bold mb-4">Create a Blog</h2>
            <form onSubmit={handleSubmit}>
                <div className="mb-4">
                    <label htmlFor="title" className="block text-gray-700 font-bold mb-2">Title</label>
                    <input
                        type="text"
                        id="title"
                        name='title'
                        className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-blue-500"
                        placeholder="Enter title"
                       onChange={handelChange}
                    />
                </div>
                <div className="mb-4">
                    <label htmlFor="description" className="block text-gray-700 font-bold mb-2">Description</label>
                    <textarea
                        id="description"
                        name='description'
                        className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-blue-500"
                        placeholder="Enter description"
                        rows="4"
                       onChange={handelChange}
                    ></textarea>
                </div>
                <div className="mb-4">
                    <label htmlFor="imageUrl" className="block text-gray-700 font-bold mb-2">Image URL</label>
                    <input
                        type="text"
                        name='image'
                        id="imageUrl"
                        className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-blue-500"
                        placeholder="Paste image URL"
                       onChange={handelChange}

                    />
                </div>
                <button
                    type="submit"
                    className="w-full bg-blue-500 text-white py-2 px-4 rounded-lg "
                >
                    Create Blog
                </button>
            </form>
        </div>
        </>
    );
};

export default CardForm;
