import React, { useState } from 'react'
import { NavLink , useNavigate } from 'react-router-dom'
import { useToast } from '@chakra-ui/react'; // Import 

import axios from 'axios';
import SuccessAlert from './BaceComp/SuccessAlert';

const Register = () => {
  const [register ,setRegister]=useState(false)
const navigate=useNavigate()

  const toast = useToast();


const [data,setData]=useState({
  name:'',
  email:'',
  password:'',
  image:null
})



const handleSubmit = async (e) => {
  e.preventDefault();
  try {
    const { name, email, password, image } = data;
    const formData = new FormData();
    formData.append('name', name);
    formData.append('email', email);
    formData.append('password', password);
    formData.append('image', image);
    const res = await axios.post('https://mernback-85dt.onrender.com/api/users/register', formData, {
      headers: {
        'Content-Type':'multipart/form-data',
      },
    });
    toast({
      title: "Register Successful!",
      status: "success",
      duration: 3000,
      isClosable: true,
    });  
    
    if (res.data.status === 200) {
      setRegister(true);
      navigate('/login'); // Navigate to the login page
    }

    
      
  } catch (error) {
    
    console.error('Error registering:', error);
  }
};

{register && <SuccessAlert/>}

  return (
    <>
    <form onSubmit={handleSubmit}>
      <div className="flex flex-col items-center justify-center mt-3 px-6 py-8 mx-auto lg:py-0">
        <div className="w-full bg-white rounded-lg shadow border md:mt-0 sm:max-w-md xl:p-0">
          <div className="p-6 space-y-4 md:space-y-6 sm:p-8">
            <p className="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl">
              Create an account
            
            
              </p><div>
                <label className="block mb-2 text-sm font-medium text-gray-900">
                  Your username
                </label>
                <input placeholder="JohnDoe" className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg block w-full p-2.5" onChange={(e)=>{setData({...data,name:e.target.value})}} id="username" type="text"/>
              </div>
              <div>
                <label className="block mb-2 text-sm font-medium text-gray-900">
                  Email
                </label>
                <input className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg block w-full p-2.5" onChange={(e)=>{setData({...data , email:e.target.value})}} placeholder="User Mail" id="password" type="email"/>
              </div>
              <div>
                <label className="block mb-2 text-sm font-medium text-gray-900">
                   password
                </label>
                <input className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg block w-full p-2.5" onChange={(e)=>{setData({...data,password:e.target.value})}} placeholder="••••••••" id="confirmPassword" type="password"/>
              </div>
              <div>
                <label className="block mb-2 text-sm font-medium text-gray-900">
                   Logo
                </label>
                <input className="bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg block w-full p-2.5" onChange={(e)=>{setData({...data,image:e.target.files[0]})}} name='image' id="confirmPassword" type="file" required/>
              </div>
              <div className="flex items-start">
                <div className="flex items-center h-5">
                </div>
                <div className="ml-3 text-sm">
                  <label className="font-light text-gray-500 text-gray-300">
                  
        <p className="text-gray-900 mt-4"> have an account? <NavLink to="/login" className="text-sm text-blue-500 -200 hover:underline mt-4">Login </NavLink></p>

                  </label>
                </div>
              </div>

              <button className="w-full bg-blue-500 hover:bg-blue-700 focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center  focus:ring-blue-800 text-white" type="submit">
                Create an account
              </button>
            
          </div>
        </div>
      </div></form>
    
    </>
  )
}

export default Register
