import React, { useState } from 'react';
import './BaseCss/Forget.css';
import axios from 'axios';
import { useToast } from '@chakra-ui/react';

const Forget = () => {
  const [email, setEmail] = useState('');
  const toast = useToast();

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const res = await axios.post('https://mernback-85dt.onrender.com/api/users/forget', { email });
      console.log(res.data);
      toast({
        title: "Email sent",
        description: "Password reset instructions sent to your email.",
        status: "success",
        duration: 5000,
        isClosable: true,
      });
    } catch (error) {
      console.error("Error:", error.response.data.error);
      toast({
        title: "Error",
        description: error.response.data.error || "An error occurred",
        status: "error",
        duration: 5000,
        isClosable: true,
      });
    }
  };

  return (
    <div className="container">
      <h1>Forget Password</h1>
      <form onSubmit={handleSubmit}> 
        <div className="form-group">
          <label htmlFor="email">Email Address</label>
          <input type="email" onChange={(e) => setEmail(e.target.value)} id="email" name="email" placeholder="Enter your email address" required/>
        </div>
        <button type="submit" className="btnf">Reset Password</button>
      </form>
      <p>Remember your password? <a href="#">Login</a></p>
    </div>
  );
};

export default Forget;
