import React, { useEffect, useState } from 'react';
import axios from 'axios';
import AllBlogCmp from './AllBlogCmp';
import HomeNav from '../HomeNav'

const My_Blog = () => {
    const [searchResults, setSearchResults] = useState([]);

    const [userId, setUserId] = useState(null);
    const [userImage, setuserImage] = useState(null);
    const [blogs, setBlogs] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');

    useEffect(() => {
        const fetchUserAndBlogs = async () => {
            try {
                const userDataString = sessionStorage.getItem('user');
                if (userDataString) {
                    const parsedUserData = JSON.parse(userDataString);
                    setUserId(parsedUserData._id);
                    setuserImage(parsedUserData.image);
                    const response = await axios.get(`https://mernback-85dt.onrender.com/api/post/getall_blogs/${parsedUserData._id}`);
                    setBlogs(response.data.data);
                    setLoading(false);
                }
            } catch (error) {
                setError('Error fetching data');
                setLoading(false);
            }
        };

        fetchUserAndBlogs();
    }, []);

    const handleSearch = async (search) => {
        try {
            const response = await axios.get(`https://mernback-85dt.onrender.com/api/post/search/${search}`);
            setSearchResults(response.data.blogs);
            console.log(response.data.blogs);
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <>

<HomeNav handleSearch={handleSearch} />

<div>
  {loading ? (
    <p>Loading...</p>
  ) : error ? (
    <p>{error}</p>
  ) : (
    <div>
      {searchResults.length > 0 ? (
        searchResults.map((post, index) => (
          <AllBlogCmp
            key={index}
            title={post.title}
            description={post.description}
            user={post.userName}
            date={post.createdAt}
            image={post.image}
            userImage={post.userImage}
            id={post._id}
          />
        ))
      ) : (
        blogs.map((blog, index) => (
          <AllBlogCmp
            key={blog._id}
            title={blog.title}
            description={blog.description}
            image={blog.image}
            date={blog.createdAt}
            userImage={userImage}
            name={blog.userName}
            id={blog._id}
          />
        ))
      )}
    </div>
  )}
</div>

        </>
    );
};

export default My_Blog;
