import React, { useEffect, useState } from 'react';
import axios from 'axios';
import AllBlogCmp from './AllBlogCmp';
import HomeNav from '../HomeNav';

const AllBlogs = () => {
  const [searchResults, setSearchResults] = useState([]);
  const [data, setData] = useState([]);

  const getAllBlog = async () => {
    const response = await axios.get('https://blogapp-gyk4.onrender.com/api/post/all-blogs');
    console.log(response.data);
    setData(response.data.blogs);
  };

  useEffect(() => {
    getAllBlog();
  }, []);

  // For Navbar Search API call
  const handleSearch = async (search) => {
    try {
      const response = await axios.get(`https://mernback-85dt.onrender.com/api/post/search/${search}`);
      setSearchResults(response.data.blogs);
      console.log(response.data.blogs);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
      <HomeNav handleSearch={handleSearch} />

      {/* Conditionally render search results if available, otherwise render default posts */}
      {searchResults.length > 0 ? (
        searchResults.map((post, index) => (
          <AllBlogCmp
            key={index}
            title={post.title}
            description={post.description}
            user={post.userName}
            date={post.createdAt}
            image={post.image}
            userImage={post.userImage}
            id={post._id}
          />
        ))
      ) : (
        data.map((blog, index) => (
          <AllBlogCmp
            key={index}
            title={blog.title}
            description={blog.description}
            user={blog.userName}
            date={blog.createdAt}
            image={blog.image}
            userImage={blog.userImage}
            id={blog._id}
          />
        ))
      )}
    </>
  );
};

export default AllBlogs;
