import React, { useState } from 'react';
import { Flex, Box, Image, Link, chakra, IconButton, Modal, ModalOverlay, ModalContent, ModalHeader, ModalFooter, ModalBody, Button, Input } from '@chakra-ui/react';
import { EditIcon, DeleteIcon } from '@chakra-ui/icons';
import axios from 'axios';

const AllBlogCmp = ({ title, description, image, user, date, userImage, id }) => {
    const [isEditing, setIsEditing] = useState(false);
    const [editedTitle, setEditedTitle] = useState(title);
    const [editedDescription, setEditedDescription] = useState(description);
    const [editedImage, setEditedImage] = useState(image);

    const deletePost = async () => {
        try {
            const res = await axios.delete(`https://mernback-85dt.onrender.com/api/post/delete/${id}`);
            console.log(res.data);
            window.location.reload();
        } catch (error) {
            console.error("Error deleting post:", error.message);
        }
    }

    const handleEdit = () => {
        setIsEditing(true);
    };

    const handleClose = () => {
        setIsEditing(false);
    };

    const handleSave = () => {
        // Here you can handle the save action, for example, call an API to update the blog post
        console.log("Saving changes...");
        setIsEditing(false);
    };

    const handelSubmit = () => {
        const res = axios.put(`https://mernback-85dt.onrender.com/api/post/update/${id}`, {
            title: editedTitle,
            description: editedDescription,
            image: editedImage
        })
        console.log(res.data);
        window.location.reload();
        // window.location.reload();

    }

    return (
        <>
            <Flex
                bg="#edf3f8"
                _dark={{
                    bg: "#3e3e3e",
                }}
                p={50}
                w="full"
                alignItems="center"
                justifyContent="center"
                className='mt-9'
            >
                <Box
                    mx="auto"
                    rounded="lg"
                    shadow="md"
                    bg="white"
                    _dark={{
                        bg: "gray.800",
                    }}
                    maxW="2xl"
                >
                    <Image
                        roundedTop="lg"
                        w="full"
                        h={64}
                        fit="cover"
                        src={image}
                        alt="Article"
                    />

                    <Box p={6}>
                        <Box>
                            <chakra.span
                                fontSize="xs"
                                textTransform="uppercase"
                                color="brand.600"
                                _dark={{
                                    color: "brand.400",
                                }}
                            >
                            </chakra.span>
                            <Link
                                display="block"
                                color="gray.800"
                                _dark={{
                                    color: "white",
                                }}
                                fontWeight="bold"
                                fontSize="2xl"
                                mt={2}
                                _hover={{
                                    color: "gray.600",
                                    textDecor: "underline",
                                }}
                            >
                                {title}

                            </Link>
                            <chakra.p
                                mt={2}
                                fontSize="sm"
                                color="gray.600"
                                _dark={{
                                    color: "gray.400",
                                }}
                            >
                                {description}
                            </chakra.p>
                        </Box>

                        <Box mt={4}>
                            <Flex alignItems="center">
                                <Flex alignItems="center">
                                    <Image
                                        h={10}
                                        fit="cover"
                                        rounded="full"
                                        src={`https://blogapp-gyk4.onrender.com/uploads/${userImage}`}
                                        alt="Avatar"
                                    />                                 <Link
                                        mx={2}
                                        fontWeight="bold"
                                        color="gray.700"
                                        _dark={{
                                            color: "gray.200",
                                        }}
                                    >
                                        {user}
                                    </Link>
                                </Flex>
                                <chakra.span
                                    mx={1}
                                    fontSize="sm"
                                    color="gray.600"
                                    _dark={{
                                        color: "gray.300",
                                    }}
                                >
                                    <b>{date}</b>
                                </chakra.span>
                                {/* Edit and Delete Buttons */}
                                <IconButton
                                    onClick={handleEdit}
                                    aria-label="Edit"
                                    icon={<EditIcon />}
                                    size="sm"
                                    variant="ghost"
                                    ml={2}
                                />
                                <IconButton
                                    onClick={deletePost}
                                    aria-label="Delete"
                                    icon={<DeleteIcon />}
                                    size="sm"
                                    variant="ghost"
                                    ml={2}
                                />
                            </Flex>
                        </Box>
                    </Box>
                </Box>
            </Flex>

            {/* Edit Modal */}
            <Modal isOpen={isEditing} onClose={handleClose}>
                <ModalOverlay />
                <ModalContent>
                    <form onSubmit={handelSubmit}>

                        <ModalHeader>Edit Blog Post</ModalHeader>
                        <ModalBody>
                            <Input
                                placeholder="Title"
                                value={editedTitle}
                                onChange={(e) => setEditedTitle(e.target.value)}
                            />
                            <Input
                                placeholder="Description"
                                value={editedDescription}
                                onChange={(e) => setEditedDescription(e.target.value)}
                            />
                            <Input
                                placeholder="Image URL"
                                value={editedImage}
                                onChange={(e) => setEditedImage(e.target.value)}
                            />
                        </ModalBody>

                        <ModalFooter>
                            <Button type='submit' colorScheme="blue" mr={3} onClick={handleSave}>
                                Save
                            </Button>
                            <Button variant="ghost" onClick={handleClose}>Cancel</Button>
                        </ModalFooter>
                    </form>

                </ModalContent>
            </Modal>
        </>
    );
}

export default AllBlogCmp;
