import React,{useState} from 'react'
import Navbar from './Navbar'
import { NavLink , useNavigate , } from 'react-router-dom'
import axios from 'axios'
import { useToast } from '@chakra-ui/react'; // Import useToast


const Login = () => {
  const toast = useToast();
const [showSuccessAlert, setShowSuccessAlert] = useState(false);


  const navigate =useNavigate();

  const [data,setData]=useState({
    email:'',
    password:''
  })

  
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post("https://mernback-85dt.onrender.com/api/users/login", data);
        setShowSuccessAlert(true);
      toast({
        title: "Login Successful!",
        status: "success",
        duration: 3000,
        isClosable: true,
      });   

      const {user , token} = response.data;
      localStorage.setItem('token',token);
      sessionStorage.setItem('user',JSON.stringify(user));
      navigate('/all_blogs');
    } catch (error) {
      
      console.error("Error logging in:", error.response.data);
    }
  }

  return (
    <>
    {/* <Navbar /> */}
    
    <div className="flex flex-col items-center justify-center h-screen">
  <div className="w-full max-w-md bg-white rounded-lg shadow-md p-6">
    <h2 className="text-2xl font-bold text-gray-900 mb-4">Login</h2>
    <form className="flex flex-col" onSubmit={handleSubmit}>
      <input type="email" onChange={(e)=>setData({...data,email:e.target.value})}  className="bg-gray-100 text-gray-900 border-0 rounded-md p-2 mb-4 focus:bg-gray-200 focus:outline-none focus:ring-1 focus:ring-blue-500 transition ease-in-out duration-150" placeholder="Email address"/>
      <input type="password" onChange={(e)=>setData({...data,password:e.target.value})} className="bg-gray-100 text-gray-900 border-0 rounded-md p-2 mb-4 focus:bg-gray-200 focus:outline-none focus:ring-1 focus:ring-blue-500 transition ease-in-out duration-150" placeholder="Password"/>
      <div className="flex items-center justify-between flex-wrap">
       
        <NavLink to="/forget" className="text-sm text-blue-500 hover:underline mb-0.5">Forgot password?</NavLink>
        <p className="text-gray-900 mt-4"> Don't have an account? <NavLink to="/register" className="text-sm text-blue-500 -200 hover:underline mt-4">Signup</NavLink></p>
      </div>
      <button type="submit" className="bg-gradient-to-r from-indigo-500 to-blue-500 text-white font-bold py-2 px-4 rounded-md mt-4 hover:bg-indigo-600 hover:to-blue-600 transition ease-in-out duration-150">Login</button>
    </form>
  </div>
</div>

    </>
  )
}

export default Login
