// import './App.css';
import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Home from './Components/Home';
import Login from './Components/Login';
import Register from './Components/Register';
import PostDetails from './Components/PostDetails';
import Protected from './Components/BaceComp/Protected';
import AllBlogs from './Components/BaceComp/AllBlogs';
import My_Blog from './Components/BaceComp/My_Blog';
import HomeNav from "./Components/HomeNav"
import Navbar from './Components/Navbar'
import CardForm from './Components/BaceComp/CardForm';
import Forget from './Components/Forget';
import ResetPass from './Components/ResetPass';

function App() {
  const token=localStorage.getItem('token');
  
  return (
    
    <Router>
      {token ? <HomeNav/> : <Navbar/>}
      <Routes>
      <Route path="/login" element={<Protected CMP={Login} />} />
        <Route path="/" element={<Protected CMP={Home} />} />
        <Route path="/create_blog" element={<Protected CMP={CardForm} />} />
        <Route path="/all_blogs" element={<Protected CMP={AllBlogs } />} />
        <Route path="/my_blogs" element={<Protected CMP={My_Blog} />} />
        <Route path="/register" element={<Register/>}/>
        <Route path="/posts/:id" element={<Protected CMP={PostDetails} />} /> 
        <Route path="/forget" element={<Forget/>} />
        <Route path="/reset_pass" element={<ResetPass/>} />
      </Routes>
    </Router>
  );
}

export default App;
